.navbar_intersys {
  height: 70px;
  background-color: white;
  font-family: Dosis, sans-serif;
}

.navbar_intersys li a, .navbar_intersys li p {
  text-transform: uppercase;
  color: #9e9e9e;
  font-size: 18px;
  font-weight: bold;
  height: 70px;
  padding: 10px 20px;
  line-height: 50px;
  margin: 0;
}

.navbar_intersys a i {
  color: #9e9e9e;
  /* line-height: 70px; */
}

.navbar_intersys li a:hover, .navbar_intersys li p:hover {
  background: #F4F6F9;
  color: #585A61;
}

.navbar_intersys li:hover a i {
  color: #585A61;
}

.navbar_logo {
  height: 35px;
  margin: 1rem;
  padding-top: 5px;
}

.navbar_intersys ul {
  margin-left: auto;
}

.navbar_intersys .col.s12{
  height: 100%;
}


